 @import "./mixin";

 #wptravel-blocks-filterable-trips a{
    text-decoration:  none !important;
}

#wptravel-blocks-filterable-trips .view-box .view-image .image-thumb img {
    height: 230px !important;
}

#wptravel-blocks-filterable-trips .wptravel-filterable-trips-grid{
    display: none;
}

#wptravel-blocks-filterable-trips .wptravel-filterable-trips-grid.active{
    display: grid;
}

#wptravel-blocks-filterable-trips {
    .wptravel-filterable-trips-filters {
        display: flex;
        justify-content: center;
        gap: 10px;
        margin-bottom: 30px;
        flex-wrap: wrap;

        .filter-btn {
            padding: 10px 20px;
            border-radius: 5px;
            border: none;
            font-size: 16px;
            background-color: transparent;
            cursor: pointer;
            transition: 0.25s ease-in-out all;

            &.active {
                background: var(--wp--preset--color--secondary);
                color: var(--wp--preset--color--contrast);
            }
        }
    }
}

