                }

                .wptravel-blocks-card-body {
                    display: flex;
                    padding: 25px;
                    height: 100%;
                    position: relative;
                    flex-direction: column;
                    justify-content: space-between;

                    .wptravel-blocks-card-body-header {
                        display: flex;
                        justify-content: space-between;
                        gap: 20px;

                        .wptravel-blocks-header-left {
                            .wptravel-blocks-card-title {
                                margin: 0;
                                // line-height: 1.4;
                                // font-size: 22px;
                            }
                    
                            .wptravel-blocks-trip-code {
                                font-size: 12px;
                                color: var(--wp--preset--color--primary);
                                margin-top: 10px;
                            }
                        }

                        .wp-travel-add-to-wishlists {
                            color: var(--wp--preset--color--primary) !important;
                            font-size: 20px;
                            position: static;
                            font-size: 0;
                            transition: 0.25s ease-in-out all;

                            &:hover {
                                transform: translateY(-3px);
                            }

                            i {
                                font-size: 20px;
                            }
                        }
                    }

                    .wptravel-blocks-card-content {
                        display: flex;
                        flex-direction: column;
                        padding: 15px 0;
                        font-size: inherit;
                    
                        .wptravel-blocks-trip-excerpt {
                            color: #777;
                            margin-bottom: 10px;
                            line-height: 1.4;

                            p {
                                margin: 0;
                            }
                        }

                        .wptravel-blocks-trip-meta-container {
                            display: grid;
                            margin: 10px 0px;
                            grid-template-columns: 1fr 1fr;
                            width: 100%;
                            gap: 5px;

                            .wptravel-blocks-trip-meta {
                                gap: 5px;
                                display: flex;
                                font-size: 13px;
                            
                                i {
                                    display: flex;
                                    width: 15px;
                                    justify-content: center;
                                    align-items: center;
                                    color: var(--wp--preset--color--primary);
                                }

                                .fixed-date-dropdown {
                                    .dropdown-content {
                                        display: grid;
                                        position: absolute;
                                        grid-template-columns: repeat(auto-fit, minmax(165px, 1fr));
                                        min-width: 150px;
                                        background-color: #FFF;
                                        border-radius: 10px;
                                        box-shadow: 0px 4px 10px 0px rgba(0,0,0,.1);
                                        overflow: hidden;
                                        z-index: 3;
                                        transition: all .2s ease-in-out;
    
                                        .dropdown-list{
                                            padding: 10px 15px;

                                            &:hover {
                                                background: #EEE;
                                                border-radius: 10px;
                                                padding-left: 20px;
                                            }
                                            &::before {
                                                background: none;
                                            }
                                        }
                                    }
                                }
                            }
                        }
                    }
                    
                    .wptravel-blocks-card-footer {
                        display: flex;
                        justify-content: space-between;
                        padding-top: 15px;
                        border-top: 1px solid #eee;
                    
                        .wptravel-blocks-footer-left {
                            display: flex;
                            flex-direction: column;
                            justify-content: center;
                            gap: 10px;

                            .wptravel-blocks-trip-rating {
                                display: flex;
                                align-items: center;
                                gap: 0px;

                                i {
                                    padding: 0px;
                                    color: orange;
                                }
                            }
                        }
                    
                        .wptravel-blocks-footer-right {
                            display: flex;
                            flex-direction: column;
                            justify-content: flex-end;
                            align-items: flex-end;
                            text-align: right;
                            
                            .wptravel-blocks-trip-original-price {
                                color: #999;
                                line-height: 1;
                                font-size: 16px;
                                font-weight: 500;
                            }
                            
                            .wptravel-blocks-trip-offer-price {
                                font-size: 22px;
                                font-weight: 500;
                                line-height: 1;
                                margin-top: 5px;
                            }
                        }
                    }
                }

                &:hover {
                    .wptravel-blocks-trip-card-img-container {
                        img {
                            scale: 1.1;
                        }
                        .wptravel-blocks-img-overlay-base {
                            .wptravel-blocks-img-overlay {
                                .favourite {
                                    opacity: 1;
                                    transform: translateY(0px);
                                    pointer-events: all;
                                }
                            }
                        }
                    }
                }
            }
        }
    }
}

#wptravel-blocks-filterable-trips {
    .wptravel-filterable-trips-grid {
        &.layout-one, &.layout-three, &.layout-four {
            .wptravel-blocks-trip-card {
                .wptravel-blocks-trip-meta {
                    @include wptravel-blocks-trip-meta;
                }
            }
        }
    }
}