@mixin wptravel-blocks-trip-meta {
    width: fit-content;
    gap: 2px;
    font-weight: 600;
    font-size: 14px;
    position: relative;

    ul {
        display: none;
        height: 0;
        opacity: 0;
        list-style: none;
        margin: 0;
        padding: 0;
        border-radius: 10px;
        transform: translateY(10px);
        transition: 0.25s ease-out transform;

        li {
            background: none;
            border: none;
        }
    }

    &:hover {
        ul {
            opacity: 1;
            height: fit-content;
            transform: translateY(0px);
            pointer-events: all;
            margin-left: 13px;
            display: block;
            position: absolute;
            top: 20px;
            background: white;
            padding: 5px 10px;
            z-index: 10;
            box-shadow: 0 0 5px 1px rgba(0,0,0,0.1);
        }
    }
}